import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewCafe from '@/components/NewCafe'
import ViewCafe from '@/components/ViewCafe'
import EditCafe from '@/components/EditCafe'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'new-cafe',
      component: NewCafe
    },    
    {
      path: '/edit/:cafe_id',
      name: 'edit-cafe',
      component: EditCafe
    },    
    {
      path: '/:cafe_id',
      name: 'view-cafe',
      component: ViewCafe
    }    
  ]
})



